import React from 'react';
import {
  Block,
  Row,
  Col,
  Button,
  Navbar,
  Card,
  CardHeader,
  CardContent,
  CardFooter,
} from 'framework7-react';
import AddRecord, { popupName } from './add-record';
import { observer } from 'mobx-react-lite';
import store from '../../../store';

import styles from './records.module.scss';

const Records = observer(() => {
  return (
  <>
    <Navbar>
      Записи
    </Navbar>
    <Block>
      <Row className="flex-direction-column align-items-stretch">
        <Col>
          <Button fill preloader popupOpen={"."+popupName}>+ Добавить</Button>
        </Col>
        <Col className={styles.records}>
          {store.records.list.map((r, i) => (
            <Card key={i} className="demo-card-header-pic">
              <CardHeader>
                Record {i}
              </CardHeader>
              <CardContent>
                <p className="date">{r.type}</p>
                <p>
                  {r.date}
                </p>
              </CardContent>
              <CardFooter>
                {/* <Link>Like</Link>
                <Link>Read more</Link> */}
              </CardFooter>
            </Card>
          ))}
        </Col>
      </Row>
    </Block>
    <AddRecord />
  </>
)
}
);

export default Records
