import React from 'react';
import {
  Block,
  // Row,
  // Col,
  Button,
  Navbar,
  NavRight,
  Link,
  Page,
  Popup,
  List,
  ListInput,
} from 'framework7-react';
import { Formik, Field } from 'formik';
import yup from 'yup'
import { observer } from 'mobx-react-lite';
import store from '../../../store'

export const popupName = 'ADD-RECORD';

const AddRecord = observer(() => {
  return (
    <Popup className={popupName} push>
      <Page>
        <Navbar title="Добавить запись">
          <NavRight>
            <Link popupClose>Закрыть</Link>
          </NavRight>
        </Navbar>

          <Formik
            initialValues={{ type: "" }}
            onSubmit={(values, actions) => {
              store.records.addRecord(values)
              actions.resetForm();
            }}
          >
            {
              ({ handleSubmit }) => (
                <>
                  <List inlineLabels noHairlinesMd>
                    <Field
                      name="type"
                      as={ListInput}
                      label="Тип"
                      type="select"
                    >
                      <option disabled value="">N/A</option>
                      <option value="fueling">Заправка</option>
                      <option value="taxi">Такси</option>
                    </Field>
                    <Field
                      name="mileage"
                      as={ListInput}
                      type="number"
                      placeholder="Пробег"
                      clearButton
                    >
                    </Field>
                    <Field
                      name="money"
                      as={ListInput}
                      type="number"
                      placeholder="Деньги"
                      clearButton
                    >
                    </Field>
                    <Field
                      name="date"
                      label="Дата"
                      as={ListInput}
                      type="datetime-local"
                    >
                    </Field>
                  </List>
                  <Button onClick={handleSubmit} popupClose>Добавить</Button>
                </>
              )
            }
          </Formik>
      </Page>
    </Popup>
  )
})

export default AddRecord;
