import React from 'react';
import {
  Page,
  Tabs,
  Tab,
  Link,
  Toolbar,
} from 'framework7-react';
import Records from './records/records'
import Debts from './Debts'
import Statistics from './Statistics';
import Debug from './Debug';

const Home = () => (
  <Page>
    <Toolbar bottom>
      <Link tabLink="#tab-1" tabLinkActive>Записи</Link>
      <Link tabLink="#tab-2" tabLinkActive>Долги</Link>
      <Link tabLink="#tab-3" tabLinkActive>Статистика</Link>
      <Link tabLink="#tab-4" tabLinkActive>Отладка</Link>
    </Toolbar>
    <Tabs animated routable>
      <Tab id="tab-1">
        <Records />
      </Tab>
      <Tab id="tab-2">
        <Debts />
      </Tab>
      <Tab id="tab-3">
        <Statistics />
      </Tab>
      <Tab id="tab-4">
        <Debug />
      </Tab>
    </Tabs>
  </Page>
);

export default Home
